
import blackLogo1 from '@/assets/blackLogo1.png'
let BMap = {};
let map = {};
let ww = [];
let infoWindows=[];
let points=[]
export default {
      data(){
        return{
          places: [],
          state1: '',
          wwid:null,
        }
      },
      methods:{
        inww(){
          this.$axios.get('http://8.130.9.237:8083/ww/findOneByPk?id='+this.wwid).then((res)=>{
            var ww = res.data;
            sessionStorage.setItem('wwinfo',JSON.stringify(ww));
            this.$router.push('/home/fadInfo')
          });
        },
        findAll(){
          this.$axios.get('http://8.130.9.237:8083/ww/findAll').then((res)=>{
            ww = res.data.results;
            for(var i = 0;i <ww.length;i++){
              var place = {
                value:ww[i].wwname,
                wwid:ww[i].wwid,
                  y:ww[i].wwlatitude1,
                  x:ww[i].wwlongitude1,
                  i:i,
              }
              this.places.push(place);
            }
          });
        },
        getAddrByPoint(point,i){
          map.centerAndZoom(point, 10);
          map.openInfoWindow(infoWindows[i],points[i]);
        },
        loadJScript() {
          var script = document.createElement('script');
          script.type = 'text/javascript';
          script.src = 'https://api.map.baidu.com/getscript?v=3.0&ak=DNMsvCZA5BgKYpCEffHexGiBkha4KVDG&services=&t=20220315120524';
          script.onload = ()=>{
            this.init();
          }
          document.body.appendChild(script);
        },
        init() {
          var that = this;
          BMap = window.BMap;
          map = new BMap.Map("allmap");    // Map
          map.centerAndZoom(new BMap.Point(115.97, 40.45), 10);  // ,
          //
          map.addControl(new BMap.MapTypeControl({
            mapTypes:[
              BMAP_NORMAL_MAP,
              BMAP_HYBRID_MAP,
            ]}));
          var navigationControl = new BMap.NavigationControl({ //
            anchor: BMAP_ANCHOR_TOP_LEFT, //
            type: BMAP_NAVIGATION_CONTROL_SMALL //SMALL
          })
          map.addControl(navigationControl); //
          var geolocationControl = new BMap.GeolocationControl({anchor: BMAP_ANCHOR_BOTTOM_LEFT}) ;//
          geolocationControl.addEventListener("locationSuccess", function(e){ //
            that.getAddrByPoint(e.point); //
          });
          geolocationControl.addEventListener("locationError",function(e){ //
            alert(e.message);
          });
          map.addControl(geolocationControl); //
          map.setCurrentCity("");          //  
          map.enableScrollWheelZoom(true);     //
        },
        addMarker(x,y,name,location1,location2,location3,location4,i){
          var myIcon = new BMap.Icon(blackLogo1, new BMap.Size(400, 400),{
            anchor: new BMap.Size(16, 20)
          });
          points[i] = new BMap.Point(x,y);
          var marker = new BMap.Marker(points[i],{ icon: myIcon }); // 
          map.addOverlay(marker);            //
          var opts = {
            width : 300,     // 
            height: 80,     // 
            title : name , // 
          }
          infoWindows[i] = new BMap.InfoWindow(":"+location1+location2+location3+location4, opts);  // 
          marker.addEventListener("click", function(){
            map.openInfoWindow(infoWindows[i],marker.point); //
          });
        },
        makePoint(){
          for(var i = 0; i < ww.length;i++){
            this.addMarker(ww[i].wwlongitude1  - 0, ww[i].wwlatitude1- 0,ww[i].wwname,ww[i].wwlocation1,ww[i].wwlocation2,ww[i].wwlocation3,ww[i].specificlocation,i);
          }
        },
        querySearch(queryString, cb) {
          var places = this.places;
          var results = queryString ? places.filter(this.createFilter(queryString)) : places;
          cb(results);
        },
        createFilter(queryString) {
          return (places) => {
            return (places.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
          };
        },
        handleSelect(item) {
          this.getAddrByPoint(new BMap.Point(item.x- 0, item.y- 0),item.i);
          this.wwid = item.wwid - 0;
        }
      },
      mounted() {
          this.findAll();
          setTimeout(()=>{
            this.loadJScript();
          },300);
          setTimeout(()=>{
            this.makePoint();
          },1000);
      },
};
